import React, { useState } from 'react';
import Axios from 'axios';

const AddComment = ({ articlName, setArticleInfo }) => {
	const [username, setUsername] = useState('');
	const [commentText, setCommentText] = useState('');

	const addComment = async () => {
		if (username == '' || commentText == '') {
			window.alert('Please fill details.');
		} else {
			const result = await Axios.post(`/api/articles/${articlName}/add-comment`, {
				username,
				text: commentText,
			});

			setArticleInfo(result.data);
			setUsername('');
			setCommentText('');
		}
	};

	return (
		<div id='add-comment-form'>
			<h3>Add a Comment</h3>
			<label htmlFor=''>
				Name:
				<input type='text' onChange={e => setUsername(e.target.value)} value={username} required />
			</label>
			<label htmlFor=''>
				Comment:
				<textarea required cols='50' rows='4' value={commentText} onChange={e => setCommentText(e.target.value)}></textarea>
			</label>
			<button onClick={() => addComment()}>Add Comment</button>
		</div>
	);
};

export default AddComment;
